import { makeStyles } from '@material-ui/core';
import React, { FC, ReactNode } from 'react';

interface Props {
  type?: string;
  href: string;
  children: ReactNode;
}

const useStyles = makeStyles({
  wrap: {
    background: '#fff',
    padding: '15px 20px',
    borderRadius: 20,
    display: 'flex',
    alignItems: 'center',
    boxShadow: '0px 2px 4px 0px rgba(0,0,0,.03)',
    cursor: 'pointer',
    textDecoration: 'none',
    transition: 'background .3s ease-in-out',

    '&:hover': {
      background: 'rgb(103 41 255 / 15%)',
    },
  },
});

const LinkAction: FC<Props> = ({ children, href, type = 'default' }) => {
  const classes = useStyles();

  return (
    <a className={classes.wrap} href={href} target="_blank">
      {children}
    </a>
  );
};

export { LinkAction };
