import { makeStyles } from '@material-ui/core';
import { get } from 'lodash';
import React, { FC } from 'react';
import { socialLists } from '../profile-item';

interface Props {
  name: string;
}

const size = 30;

const useStyle = makeStyles({
  wrap: {
    height: 50,
    width: 50,
    borderRadius: 10,
    background: '#fff',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    overflow: 'hidden',
  },

  img: {
    display: 'block',
    width: size,
    height: size,
    borderRadius: 5,
  },
});

const Icon: FC<Props> = ({ name }) => {
  const classes = useStyle();

  const style = {
    background: get(socialLists, `${name}.colorLight`),
  };

  return (
    <div className={classes.wrap} style={style}>
      <img
        className={classes.img}
        src={get(socialLists, `${name}.icon`)}
        alt={name}
      />
    </div>
  );
};

export { Icon };
