import { makeStyles } from '@material-ui/core';
import { get } from 'lodash';
import React, { FC } from 'react';
import { Icon } from '../icons';
import { LinkAction } from '../link-action';
import { socialLists } from './social-list';

interface Props {
  contact: any;
}

const useStyle = makeStyles({
  content: {
    paddingLeft: 15,
  },

  nameContact: {
    fontWeight: 500,
    fontSize: 18,
  },

  contentContact: {
    marginTop: 5,
    color: '#000',
    fontSize: 14,
  },
});

const ProfileItem: FC<Props> = ({ contact }) => {
  const classes = useStyle();

  const contactItem = get(socialLists, contact.name);

  const styleName = {
    color: get(contactItem, `color`),
  };

  return (
    <LinkAction href={contact.address}>
      <Icon name={contact.name} />
      <div className={classes.content}>
        <div className={classes.nameContact} style={styleName}>
          {contactItem.name}
        </div>
        <div className={classes.contentContact}>{contact.address}</div>
      </div>
    </LinkAction>
  );
};

export { ProfileItem };
