import { Avatar, makeStyles } from '@material-ui/core';
import React, { FC } from 'react';
import { useAuth } from '../../../../context';

interface Props {}

const useStyles = makeStyles({
  wrap: {
    background: '#6729FF',
    padding: 20,
    borderRadius: 15,
    color: '#fff',
  },

  name: {
    fontWeight: 500,
    fontSize: 30,
    margin: '15px 0',
  },

  description: {
    lineHeight: 1.5,
    fontSize: 14,
  },
});

const ProfileHeading: FC<Props> = () => {
  const { user } = useAuth();

  const classes = useStyles();

  return (
    <div className={classes.wrap}>
      <Avatar />

      <div className={classes.name}>{user.fullname}</div>
      <div className={classes.description}>{user.description}</div>
    </div>
  );
};

export { ProfileHeading };
